const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			navList: [
				{ id: '3', img: 'iconbulletin', title: '微金公告' },
				{ id: '6', img: 'iconqiapiansousuo', title: '论坛搜索' },
				{ id: '2', img: 'iconliebiao', title: '贷款产品交流' },
				
				{ id: '4', img: 'iconyonghu', title: '信用卡交流' },
				{ id: '5', img: 'iconqiapiansousuo', title: '推荐热帖' },
				{ id: '1', img: 'iconxiepinglun', title: '网友交流' },
				// { id: '1', img: 'iconxiepinglun', title: '网友交流' },
				// // { id: '2', img: 'iconliebiao', title: '拒贷汇总' },
				// { id: '3', img: 'iconbulletin', title: '微金公告' },
				// // { id: '4', img: 'iconyonghu', title: '从业感悟' },
				// { id: '5', img: 'iconqiapiansousuo', title: '论坛搜索' }
			],
			Inv: 0,
			pageData: '',
			imgUrl:'',
			page: '1',
			boardId:'',
			isShow: false,
			categoryList:[
				{id:1,title:'直辖市'},
				{id:2,title:'华东地区'},
				{id:3,title:'华北地区'}
			],
			subCategoryList:[
				{id:'1',img: '../static/imgLost.png',title:'北京'},
				{id:'1',img: '../static/imgLost.png',title:'北京'},
				{id:'1',img: '../static/imgLost.png',title:'北京'},
				{id:'1',img: '../static/imgLost.png',title:'北京'},
			],
			categoryActive: 0,
		};
	},
	onLoad() {
		this.getUserInfo()
		// 加载微金交流首页数据
		this.getIndexData();
		this.imgUrl = helper.imgUrl
	},
	methods: {
		// 左边导航点击事件
		categoryMainClick(e,index){
			console.log(e,index);
			this.categoryActive = index;
			// uni.navigateTo({
			// 	url:`/pages/articleDetail?id=${e.id}`
			// })
		},
		selListType(e) {
			this.Inv = e.currentTarget.dataset.index
			this.boardId = e.currentTarget.dataset.block_id
			this.page = '1'
		},
		goDetail(e){
			uni.navigateTo({
				url:`/pages/articleDetail?id=${e}`
			})
		},
		// 获取用户信息
		getUserInfo(){
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					let user = res.data.type
					if(user != 'member'){
						this.isShow = true
					}else{
						this.isShow = false
					}
					// this.userInfo = res.data
				}
			})
		},
		// 数据
		getIndexData() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/forum/index`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.pageData = res.data.data;
						this.boardId = res.data.data.board_data[0].id
						console.log(this.boardId,'88')
						
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
		// 导航子页面跳转
		getNav(e) {
			console.log(e);
			let id = e;
			if(id== '3'){
				uni.navigateTo({
					url: `/pages/indexA?id=${23}&name=微金公告`
				});
			}
		},
		// 加载下一页
		onReachBottom(){
			this.page ++;
			this.getList()
		},
		getList() {
			uni.showLoading({
				title: '加载中...',
				duration: 1000000
			});
			uni.request({
				url: `${helper.requestUrl}/forum/posts`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				data:{
					board_id: this.boardId,
					page: this.page,
					page_size: '10'
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res);
					console.log(res);
					if (res.data.status_code == '200') {
						this.pageData.board_data[this.Inv].posts = this.pageData.board_data[this.Inv].posts.concat(res.data.data);
					} else {
						uni.showToast({
							title: res.data.message,
							icon: 'none'
						});
					}
				}
			});
		},
	}
};
